import { BaseUser, UserStore } from "./user";
import { DecId } from "./core";
import { isString } from "util";
import { get } from "mobx";
import { HashMap, safeAssign } from "./utils";

type NameSet = HashMap<string>;

export type BaseUser = {
  selectedNameSet: string;
  nameSets: HashMap<NameSet>;
};

export type InitUser = Partial<BaseUser>;

export type UserComputed = Readonly<{
  nameSet: NameSet;
  getName: { (path: DecId): string | undefined };
}>;

export type UserStore = BaseUser & UserComputed;

const defaults: BaseUser = {
  selectedNameSet: "en-US",
  nameSets: { "en-US": {} }
};

const init = (initial: InitUser): BaseUser => safeAssign(defaults, initial);

const derive = (self: UserStore): UserComputed => ({
  get nameSet() {
    return self.nameSets[self.selectedNameSet];
  },
  getName(path) {
    return getName(self.nameSet, path);
  }
});

export const userDef = { init, derive };

/********** Utility Functions **********/

export function getName(nameSet: object, pathOrKey: DecId): string | undefined {
  // TODO: handle number indexes?
  if (!isString(pathOrKey)) {
    pathOrKey = pathOrKey.join() ;
  }
  if (pathOrKey.endsWith("R")) {
    return "←";
  }
  const actualName = get(nameSet, pathOrKey);
  if (actualName) {
    return actualName;
  }
  // TODO: clone name logic below is likely out of date
  const dotIndex = pathOrKey.indexOf(".");
  if (dotIndex !== -1) {
    // TODO: handle more than one character ids!
    const cloneId = pathOrKey[dotIndex + 1];
    const origKey = pathOrKey.substr(0, dotIndex) + pathOrKey.substr(dotIndex + 2);
    const origName = get(nameSet, origKey);
    if (origName) {
      const cloneName = `${origName}.${cloneId}`;
      return cloneName;
    }
  }
  return;
}



