import { ChooserStore } from "./chooser";
import { RefPath, FullDec, relativize, isFullDecList } from "./core";
import { Cell, BaseUI, UIStore, InitUI, formatResult, InputKeyMap, uiDef, colors } from "./ui";
import { assocIn } from "icepick";
import { makeAllParamCells } from "./cells";
import { safeAssign } from "./utils";

export type BaseChooser = BaseUI & {
  filter: string;
  editingFilter: boolean;
  forPath: RefPath | null;
  selectedRef: string[];
};

export type InitChooser = Partial<BaseChooser>;

export type ChooserComputed = Readonly<{
  forPathKey: string;
}>;

export type ChooserStore = UIStore &
  BaseChooser &
  ChooserComputed & {
    parent: {
      handleChooser: { (path?: RefPath): void };
    };
  };

const width = 10;

const init = (initial: InitChooser = {}): BaseChooser =>
  uiDef.init(
    safeAssign(
      <InitChooser>{
        filter: "",
        editingFilter: false,
        forPath: null
      },
      initial
    )
  );

const derive = (self: ChooserStore) =>
  safeAssign(
    uiDef.derive(self),
    <ChooserComputed>{
      get forPathKey() {
        if (!self.forPath) {
          return "";
        }
        return self.forPath.join() ;
      }
    },
    <InitUI>{
      get baseCells() {
        const { filter, forPath, repo, user } = self;
        if (!forPath) {
          return [];
        }
        const cells: Cell[] = [];
        let x = 0;
        let y = 0;
        const filterCell = {
          x,
          y,
          key: `CHSR-FLTR`,
          text: filter || "(No filter set)",
          fill: "hsl(0,0%,48%)",
          width
        };
        cells.push(filterCell);
        y++;
        const forPathKey = forPath.join() ;
        const getResultsFromDecList = (decList: FullDec[]) => {
          for (const dec of decList) {
            const { path } = dec;
            const pathKey = path.join() ;
            if (forPathKey.startsWith(pathKey)) {
              const { line } = dec;
              if (isFullDecList(line)) {
                getResultsFromDecList(line);
              }
            }
            const name = user.getName(pathKey) || "";
            const result = formatResult(repo.gen(path));
            if (!name.toLowerCase().includes(filter)) {
              continue;
            }
            const text = name ? `${name} = ${result}` : result;
            const resultCell = {
              x,
              y,
              key: `CHSR-REF-${pathKey}`,
              text,
              width,
              fill: colors.ref,
              path
            };
            cells.push(resultCell);
            y++;
          }
        };
        const { line } = repo.full;
        if (isFullDecList(line)) {
          getResultsFromDecList(line);
        }
        const paramCells = makeAllParamCells(user, forPath.slice(0, -1));
        let paramY = 0;
        for (const cell of paramCells) {
          cell.x = width;
          cell.width = width;
          cell.y = paramY++;
        }
        cells.push(...paramCells);
        return cells;
      },
      get input() {
        if (self.editingFilter) {
          return self.filter;
        }
        return null;
      },
      handleInput(e) {
        self.filter = (e.target as any).value;
      },
      get keyMap() {
        const { forPath } = self;
        if (!forPath) {
          return self.baseKeyMap;
        }
        if (self.editingFilter) {
          return <InputKeyMap>{
            title: "Set Search Filter",
            onKey(e) {
              if (e.keyCode === 13) {
                self.editingFilter = false;
                if (self.cells.length > 1) {
                  self.selectedCellIndex = 1;
                }
              }
            }
          };
        }
        let keyMap = self.baseKeyMap;
        keyMap = assocIn(keyMap, [2, 6], {
          label: "Change Filter",
          action() {
            self.selectedCellIndex = 0;
            self.editingFilter = true;
          }
        });
        keyMap = assocIn(keyMap, [3, 9], {
          label: "Cancel",
          action: () => {
            self.parent.handleChooser();
            self.forPath = null;
          }
        });
        if (self.selectedCellIndex) {
          keyMap = assocIn(keyMap, [2, 7], {
            label: "Choose",
            action() {
              const { path } = self.selectedCell;
              if (!path) {
                return;
              }
              const relPath = relativize(forPath, path);
              self.parent.handleChooser(relPath);
              self.forPath = null;
            }
          });
        }
        return keyMap;
      }
    }
  );

export const chooserDef = { init, derive };



