import * as React from "react";
import { findDOMNode } from "react-dom";
import { observer } from "mobx-react";
import styled from "styled-components";

import { colors, Cell } from "./ui";
import { keyYUnit, maxWidth } from "./react-keyboard";
import { CSSProperties } from "react";

const yScale = 0.85;
const defaultWidth = 1; // TODO: add defaultHeight
const xUnit = 18;
const yUnit = xUnit * yScale;
const spacer = 0.1 * xUnit;

const baseCellStyle: CSSProperties = {
  transition: "0.1s",
  position: "absolute",
  width: defaultWidth * xUnit,
  height: yUnit - spacer,
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  textOverflow: "clip",
  borderRadius: 3
};

// const lineStyle = {
//   position: "absolute",
//   background: colors.comment,
//   width: 6 * spacer,
//   height: 4 * spacer,
//   zIndex: -1,
// };

const cursorStyle = {
  border: "3px solid hsla(60,99%,77%,0.8)",
  background: "none",
  zIndex: 3,
  color: colors.label
};

const emptyObj = {};

const Input = observer(
  class extends React.Component {
    componentDidMount() {
      const me = findDOMNode(this) as HTMLInputElement;
      me.focus();
      me.select();
    }
    render() {
      const { style, ...rest } = this.props as any;
      return <input {...rest} style={{ ...style, background: colors.text }} />;
    }
  }
);

