const hackyScrollElId = "scrollEl";

class ScrollIn extends React.Component {
  attach() {
    const self = this as any;
    self.me = findDOMNode(self);
    const { me } = self;
    const scrollEl = document.getElementById(hackyScrollElId);
    if (!scrollEl) {
      return;
    }
    self.handleMove = (e?: TransitionEvent) => {
      if (e && e.propertyName !== "top") {
        return;
      }
      const step = (units: number) => {
        const increment = units / 9;
        let steps = 9;
        const subStep = () =>
          requestAnimationFrame(() => {
            scrollEl.scrollTo(0, scrollEl.scrollTop + increment);
            if (steps--) {
              subStep();
            }
          });
        subStep();
      };
      const { top } = me.getBoundingClientRect();
      // TODO: fix jittery-ness
      if (top < 50) {
        step(Math.min(-50, top));
      } else {
        const upper = scrollEl.offsetHeight - 4 * keyYUnit;
        if (top > upper) {
          step(Math.max(50, top - upper));
        }
      }
    };
    me.addEventListener("transitionend", self.handleMove);
    self.handleMove();
  }
  detach() {
    const self = this as any;
    self.me.removeEventListener("transitionend", self.handleMove);
  }
  componentDidMount() {
    this.attach();
  }
  getSnapshotBeforeUpdate(prevProps: any) {
    if (prevProps.Tag !== (this.props as any).Tag) {
      this.detach();
    }
    return null;
  }
  componentDidUpdate(prevProps: any) {
    if (prevProps.Tag !== (this.props as any).Tag) {
      this.attach();
    }
  }
  componentWillUnmount() {
    this.detach();
  }
  render() {
    const self = this as any;
    const { Tag = "div", ...other } = self.props;
    return <Tag {...other} />;
  }
}

const Cursor = observer(({ value, ...other }) => {
  const Tag = value != null ? Input : "div";
  return <ScrollIn Tag={Tag} {...other} value={value} />;
});

const parenCss = `
  content: "";
  position: absolute;
  box-sizing: border-box;
  border-style: solid;
  border-radius: 1.5px;
  border-top-width: 2px;
  border-bottom: none;
  border-color: hsla(0,0%,100%,0.9);
  top: -1px;
  width: ${xUnit / 3}px;
  height: ${yUnit / 2}px;
  z-index: 1
`;

const StartBox = styled.div`
  &::after {
    ${parenCss};
    left: -1px;
    border-left-width: 2px;
    border-right-width: 0;
  }
`;
const EndBox = styled.div`
  &::after {
    ${parenCss};
    right: -1px;
    border-right-width: 2px;
    border-left-width: 0;
  }
`;
const BothBox = styled.div`
  &::before {
    ${parenCss};
    left: -1px;
    border-left-width: 2px;
    border-right-width: 0;
  }
  &::after {
    ${parenCss};
    right: -1px;
    border-right-width: 2px;
    border-left-width: 0;
  }
`;

function angle(cx: number, cy: number, ex: number, ey: number) {
  const dy = ey - cy;
  const dx = ex - cx;
  let theta = Math.atan2(dy, dx); // range (-PI, PI]
  theta *= 180 / Math.PI; // rads to degs, range (-180, 180]
  // if (theta < 0) theta = 360 + theta; // range [0, 360)
  return theta;
}

const square = (x: number) => Math.pow(x, 2);

const leftGridLineBorder = `1px solid ${colors.comment}`;

const connectorHueMap = {};
let connectorHueSeed = 0;

const BaseBox: any = (props: any) => {
  const { cell, store } = props;
  if (!cell) {
    return null;
  }
  // TODO: maybe pull out all this mapping logic and make the React stuff even dumber...
  const {
    x,
    y,
    width,
    fill,
    color,
    key,
    text,
    cursor,
    parens,
    separateRight,
    leftGridLine,
    connectFrom,
    alignRight,
    display
  } = cell as Cell;

  if (leftGridLine) {
    return (
      <div
        style={{
          position: "absolute",
          zIndex: -3,
          top: y * yUnit,
          left: x * xUnit,
          height: yUnit,
          borderLeft: leftGridLineBorder
        }}
      />
    );
  }

  const style: CSSProperties = {
    ...baseCellStyle,
    width: (width || defaultWidth) * xUnit,
    background: fill,
    color: color || colors.text,
    ...(cursor ? cursorStyle : emptyObj)
  };
  if (cursor) {
    style.left = x * xUnit;
    style.top = y * yUnit - spacer / 2;
  } else {
    style.transform = `translate(${x * xUnit}px,${y * yUnit}px)`;
  }
  if (separateRight) {
    (style.width as number) -= spacer;
  }
  if (alignRight) {
    style.justifyContent = "flex-end";
    style.paddingRight = spacer;
  }
  if (!display) {
    style.cursor = "pointer";
  }
  if (!fill) {
    style.height = yUnit;
    style.fontWeight = 600;
    style.color = style.color || colors.label;
  }

  const boxProps: any = { key, style };
  if (cursor) {
    boxProps.value = store.input;
    boxProps.onChange = store.handleInput;
    return <Cursor {...boxProps} />;
  } else {
    // TODO: move this logic into store?
    boxProps.onClick = () => {
      const isAtXY: { (c: Cell): boolean } = c => !c.display && c.x === x && c.y === y;
      const clickedCellIndex = store.focusedStore.cells.findIndex(isAtXY);
      if (clickedCellIndex !== -1) {
        store.focusedStore.selectedCellIndex = clickedCellIndex;
      }
    };
  }

  let Tag: any = "div";
  boxProps.children = text;
  if (parens) {
    const [parenType] = Object.values(parens);
    switch (parenType) {
      case "s":
        Tag = StartBox;
        break;
      case "e":
        boxProps.style.zIndex = 1; // HACK: so next cell doesn't cover the end param (damn you z-index!)
        Tag = EndBox;
        break;
      case "b":
        boxProps.style.zIndex = 1; // HACK: so next cell doesn't cover the end param (damn you z-index!)
        Tag = BothBox;
        break;
    }
  }

  const element = <Tag {...boxProps} />;
  if (connectFrom) {
    const lineAngle = angle(x, yScale * y, connectFrom.x, yScale * connectFrom.y);
    const { id } = connectFrom;
    if (!(id in connectorHueMap)) {
      connectorHueMap[id] = connectorHueSeed++;
    }
    const background = `hsl(${((connectorHueMap[id] + 1) * 139) % 360},99%,79%)`; // scatters adjacent colors to prevent confusion
    const width =
      Math.sqrt(square(x - connectFrom.x) + square(yScale * (y - connectFrom.y))) * xUnit;
    const connector = (
      <div
        key={connectFrom.id}
        style={{
          zIndex: -2,
          position: "absolute",
          background,
          transformOrigin: "0 50%",
          width,
          height: 6,
          transformStyle: "flat",
          transform: `translate(${(connectFrom.x + 0.5) * xUnit}px,${(connectFrom.y + 0.5) * yUnit -
            3}px) rotate(${lineAngle + 180}deg)`
        }}
      />
    );
    return [connector, element];
  }
  return element;
};

const ReactBox = observer(BaseBox);

const ReactCells = observer(({ cells, store }) => {
  let throwawayIdCounter = 0;
  let layerIdCounter = 0;
  const renderCell = (cell: Cell) => {
    const key = cell
      ? layerIdCounter ? `L${layerIdCounter}/${cell.key}` : cell.key
      : throwawayIdCounter++;
    const { cells, x, y } = cell;
    if (!cells) {
      return <ReactBox key={key} cell={cell} store={store} />;
    }
    let minX = 0;
    let maxX = 0;
    let minY = 0;
    let maxY = 0;
    for (const cell of cells) {
      const { x, y, width = 1, height = 1 } = cell;
      if (x < minX) {
        minX = x;
      }
      if (x + width > maxX) {
        maxX = x + width;
      }
      if (y < minY) {
        minY = y;
      }
      if (y + height > maxY) {
        maxY = y + height;
      }
    }
    const width = xUnit * (maxX - minX);
    const height = yUnit * (maxY - minY);
    return (
      <div
        key={key}
        style={{
          position: "relative",
          background: "#555",
          zIndex: 4,
          width,
          height,
          boxShadow: "0 0 0 2px #555",
          transform: `translate(${x * xUnit}px,${y * yUnit}px)`
        }}
      >
        <ReactCells cells={cells} store={store} />
      </div>
    );
  };
  return cells.map(renderCell);
});

const responsiveHeight = `calc(100vh - ${3 * keyYUnit}px`;

class Responsive extends React.Component {
  me: HTMLElement | undefined;
  state = { fullHeight: false };
  updateHeight = () => {
    const { me } = this;
    if (!me) {
      return;
    }
    const { fullHeight } = this.state;
    const { offsetWidth } = me;
    if (fullHeight) {
      if (offsetWidth <= maxWidth) {
        this.setState({ fullHeight: false });
      }
    } else {
      if (offsetWidth > maxWidth) {
        this.setState({ fullHeight: true });
      }
    }
  };
  componentDidMount() {
    this.me = findDOMNode(this) as HTMLElement;
    window.addEventListener("resize", this.updateHeight);
    this.updateHeight();
  }
  componentWillUnmount() {
    const { me } = this;
    if (!me) {
      return;
    }
    window.removeEventListener("resize", this.updateHeight);
  }
  render() {
    const style: CSSProperties = {
      textAlign: "center",
      overflowX: "hidden",
      transform: "translate(0)",
      height: this.state.fullHeight ? "100vh" : responsiveHeight
    };
    return <div {...this.props} style={style} id={hackyScrollElId} />;
  }
}

// TODO: better name
export const ReactView = observer(({ store }) => {
  const { activeCells, cells } = store;
  const cellsToRender = activeCells || cells;
  const lastRegCell = cells[cells.length - 2];
  const height = lastRegCell
    ? (lastRegCell.y + (lastRegCell.height || 1)) * yUnit + 3 * keyYUnit
    : 11;
  return (
    <Responsive>
      <div style={{ position: "relative", height }}>
        <ReactCells cells={cellsToRender} store={store} />
      </div>
    </Responsive>
  );
});



