import * as React from "react";
import { render } from "react-dom";

import { App, LOCAL_STORAGE_KEY, unpackApp } from "./app";
import { ReactGUI } from "./react-gui";
// import registerServiceWorker from "./registerServiceWorker";
import "./index.css";
import { snapshot } from "./_testdata";

// registerServiceWorker();

const w: any = window;

if (process.env.NODE_ENV === "development") {
  const importTask: any = import("./_test");
  (async () => {
    const saved = localStorage.getItem(LOCAL_STORAGE_KEY);
    let store;
    if (saved) {
      store = App(unpackApp(JSON.parse(saved)));
    } else {
      w.d = await importTask;
      store = w.d.defaultStore;
    }
    render(<ReactGUI store={store} />, document.getElementById("root") as HTMLElement);
  })();
} else {
  const storedSnapshot = localStorage.getItem(LOCAL_STORAGE_KEY);
  const snapshotToUse = storedSnapshot ? JSON.parse(storedSnapshot) : snapshot;
  const store = App(unpackApp(snapshotToUse));
  render(<ReactGUI store={store} />, document.getElementById("root") as HTMLElement);
}



