const stringifyObject = require("stringify-object");

import { UIStore } from "./ui";

import { InitRepo, RepoStore, FullDec, RefPath } from "./core";
import { InitUser, UserStore } from "./user";
import { isFunction, isObject, isString, isError } from "util";
import { HashMap, safeAssign } from "./utils";

const moveStepUnit = 0.5;

export const colors: HashMap<string> = {
  op: " hsl(135,75%,44%)",
  val: "hsl(203,55%,53%)",
  arg: "hsl(38,93%,45%)",
  ref: "hsl(277,50%,57%)",
  pkg: "",
  sample: "",
  dec: "hsl(281,77%,91%)",
  param: "hsl(41,100%,72%)",
  text: "#eee",
  label: "#333",
  comment: "#888",
  error: "hsl(9,100%,69%",
  unknown: "",
  placeholder: "#555"
};

export const calcWidth = (text?: string) =>
  !isString(text) ? 1 : Math.ceil((text.length + 1) / 4);

export function formatResult(result: any): string {
  if (isError(result)) {
    return result.message || result.name;
  }
  if (isFunction(result)) {
    // TODO: return something cool like graph or animation
    return "f";
  }
  if (isObject(result)) {
    try {
      return JSON.stringify(result);
    } catch (ex) {
      return "{CIRCULAR}";
    }
  }
  return String(result);
}

export function getTextProps(value: any): ViewCell {
  const text = formatResult(value);
  const props: ViewCell = { text, width: calcWidth(text) };
  if (isError(value)) {
    props.color = colors.error;
  }
  return props;
}

export type BaseCell = {
  x: number;
  y: number;
  key: string;
};

export type Paren = "s" | "e" | "b";

export type Connector = {
  id: string;
  x: number;
  y: number;
};

export type ViewCell = Partial<{
  width: number;
  height: number;
  fill: string;
  color: string;
  text: string;
  cursor: boolean;
  display: boolean;
  parens: HashMap<Paren>;
  separateLeft: boolean;
  separateRight: boolean;
  leftGridLine: boolean;
  connectFrom: Connector;
  alignRight: boolean;
}>;

export type SamplePath = {
  path: string[];
  index: number;
};

export type Cell = BaseCell &
  ViewCell &
  Partial<{
    // TODO: move these into separate derived Cell models
    cells: Cell[];
    path: RefPath;
    gotoCellKey: string;
    editableName: boolean;
    isDecList: boolean;
    dec: FullDec;
    index: number;
    isLine: boolean;
    samplePath: string[];
  }>;

type Action = { (): void };

export type KeyCell = {
  label: string;
  action: Action | Action[];
};

export type XKeyMap = Partial<{
  1: KeyCell;
  2: KeyCell;
  3: KeyCell;
  4: KeyCell;
  5: KeyCell;
  6: KeyCell;
  7: KeyCell;
  8: KeyCell;
  9: KeyCell;
}>;

export type CellKeyMap = Partial<{
  0: XKeyMap;
  1: XKeyMap;
  2: XKeyMap;
  3: XKeyMap;
}>;

export type KeyHandler = { (e: KeyboardEvent): void };

export type InputKeyMap = Partial<{
  title: string;
  enter: string;
  esc: string;
  tab: string;
}> & {
  onKey: KeyHandler;
};

export function isInputKeyMap(keyMap: KeyMap): keyMap is InputKeyMap {
  return Boolean(keyMap && (keyMap as InputKeyMap).onKey);
}

export type KeyMap = CellKeyMap | InputKeyMap | KeyHandler;

export type BaseUI = {
  context: Partial<{
    user: InitUser;
    repo: InitRepo;
  }>;
  selectedCellIndex: number;
  input: string | null;
  keyMap: KeyMap;
  handleInput: KeyHandler;
  baseCells: Cell[];
  activeCells: Cell[];
  focusedStore: UIStore;
};

export type InitUI = Partial<BaseUI>;

// TODO: figure out what should really go here and what should go up there
export type UIComputed = Readonly<{
  baseKeyMap: KeyMap;
  cells: Cell[];
  selectedCell: Cell;
  cursorCell: Cell;
  moveBy: { (step?: number, axis?: string): void };
  moveUp: { (): void };
  moveDown: { (): void };
  moveLeft: { (): void };
  moveRight: { (): void };
  cellMap: {}; // TODO: strongly type this!
}>;

export type UIStore = BaseUI &
  UIComputed & {
    user: UserStore;
    repo: RepoStore;
  };

type Axis = "x" | "y";

const init = (initial: InitUI) => safeAssign({ selectedCellIndex: 0 }, initial);

let cursorIdCounter = 0;
const derive = (self: UIStore) => {
  cursorIdCounter++;
  return <UIComputed>{
    get user() {
      return self.context.user;
    },
    get repo() {
      return self.context.repo;
    },
    get baseCells() {
      return [] as Cell[];
    },
    get selectedCell() {
      const { baseCells } = self;
      let i = self.selectedCellIndex;
      let foundCell: Cell = baseCells[i || 0]; // TS checker couldn't infer default zero from above
      while ((!foundCell || foundCell.display) && i >= 0) {
        foundCell = baseCells[i--];
      }
      if (process.env.NODE_ENV === "DISABLED_development") {
        console.debug(stringifyObject(foundCell, { inlineCharacterLimit: 111 }));
      }
      return foundCell || baseCells[0] || {};
    },
    get cursorCell() {
      const { input, selectedCell } = self;
      if (!selectedCell) {
        return null;
      }
      const { x, y, width } = selectedCell;
      return {
        x,
        y,
        width,
        input,
        cursor: true,
        key: String(cursorIdCounter)
      };
    },
    get cells() {
      return self.baseCells.concat(self.cursorCell);
    },
    get cellMap() {
      const cMap = {
        y: {},
        x: {}
      };
      const yx = cMap.y;
      const xy = cMap.x;
      const { baseCells } = self;
      const { length } = baseCells;
      for (let i = 0; i < length; i++) {
        const cell = baseCells[i];
        if (cell.display) {
          continue;
        }
        const { x, y, width = 0, height = 0 } = cell;
        for (let cy = y; cy <= y + height; cy++) {
          for (let cx = x; cx <= x + width; cx++) {
            if (!yx[cy]) {
              yx[cy] = { min: 0, max: 0 };
            }
            if (!xy[cx]) {
              xy[cx] = { min: 0, max: 0 };
            }
            // if (cx in yx[cy]) {
            //   console.warn("Duplicate!");
            // }
            // if (cy in xy[cx]) {
            //   console.warn("Duplicate!");
            // }
            yx[cy][cx] = i;
            xy[cx][cy] = i;
            if (cx > yx[cy].max) {
              yx[cy].max = cx;
            }
            if (cx < yx[cy].min) {
              yx[cy].min = cx;
            }
            if (cy > xy[cx].max) {
              xy[cx].max = cy;
            }
            if (cy < xy[cx].min) {
              xy[cx].min = cy;
            }
          }
        }
      }
      // TODO: logic to fill out "edge" cells for less jumpiness
      return cMap;
    },
    get baseKeyMap() {
      return {
        1: {
          2: { label: "▲", action: self.moveUp },
          9: {
            label: "Undo",
            action() {
              self.repo.commits.pop();
            }
          }
        },
        2: {
          0: {
            label: "Jump to Top",
            action() {
              self.selectedCellIndex = 0;
            }
          },
          1: { label: "◀", action: self.moveLeft },
          2: { label: "▼", action: self.moveDown },
          3: { label: "▶", action: self.moveRight }
        },
        3: {
          0: {
            label: "Jump to End",
            action() {
              const { baseCells } = self;
              if (!baseCells) {
                return;
              }
              let i = baseCells.length;
              let toCellIndex = -1;
              while (toCellIndex < 0 && --i) {
                if (!self.baseCells[i].display) {
                  toCellIndex = i;
                }
              }
              self.selectedCellIndex = toCellIndex;
            }
          }
        }
      };
    },
    moveBy(step: number = +moveStepUnit, axis: Axis = "x") {
      if (self.selectedCell == null) {
        return;
      }
      if (axis === "y") {
        // debugger;
      }
      const crossAxis = axis === "x" ? "y" : "x";
      const currentCell = { ...self.selectedCell };
      // const crossSizeProp = crossAxis === "x" ? "width" : "height";
      // TODO: center-finding can likely be improved (maybe try banker's rounding to prevent cursor "drift?")
      // const crossCenter = Math.ceil(
      //   currentCell[crossAxis] + ((currentCell[crossSizeProp] || 0) - 1) / 2
      // );
      // const crossCenter =
      //   Math.ceil((currentCell[crossAxis] + (currentCell[crossSizeProp] || 0) / 2) / step) * step;
      // console.log(currentCell[crossAxis], crossCenter);
      // currentCell[crossAxis] = crossCenter;
      const crossAxisMap = self.cellMap[crossAxis];
      if (!currentCell) {
        return;
      }
      const crossAxisPos = currentCell[crossAxis];
      const crossAxisSet = crossAxisMap[crossAxisPos];
      if (!crossAxisSet) {
        return;
      }
      let axisPos = currentCell[axis];
      const { min } = crossAxisSet;
      const { max } = crossAxisSet;
      while (min <= axisPos && axisPos <= max) {
        axisPos += step;
        const foundIndex = crossAxisSet[axisPos];
        if (foundIndex !== self.selectedCellIndex && foundIndex !== undefined) {
          self.selectedCellIndex = foundIndex;
          return;
        }
      }
      if (axisPos > max) {
        let foundCurrent = false;
        for (const crossAxisPosKey in crossAxisMap) {
          if (crossAxisPos === parseInt(crossAxisPosKey)) {
            foundCurrent = true;
          } else if (foundCurrent) {
            const posList: number[] = Object.values(crossAxisMap[crossAxisPosKey]);
            // third-from-last item is actual last pos of row; last two items are min/max of row
            const wrapCellIndex = posList[posList.length - 3];
            self.selectedCellIndex = wrapCellIndex;
            return;
          }
        }
      }
      if (axisPos < min) {
        let prevKey = String(crossAxisPos);
        for (const crossAxisPosKey in crossAxisMap) {
          if (crossAxisPos === parseInt(crossAxisPosKey)) {
            const posList: number[] = Object.values(crossAxisMap[prevKey]);
            const wrapCellIndex = posList[0];
            self.selectedCellIndex = wrapCellIndex;
            return;
          } else {
            prevKey = crossAxisPosKey;
          }
        }
      }
    },
    moveRight() {
      self.moveBy();
    },
    moveLeft() {
      self.moveBy(-moveStepUnit);
    },
    moveDown() {
      self.moveBy(+moveStepUnit, "y");
    },
    moveUp() {
      self.moveBy(-moveStepUnit, "y");
    },
    get keyMap() {
      return self.baseKeyMap;
    },
    handleInput() {
      return;
    },
    get activeCells() {
      return self.cells;
    }
  };
};

export const uiDef = { init, derive };



