import { RefPath } from "./core";
import { UserStore } from "./user";
import { getTextProps, colors } from "./ui";

export function makeParamCells(user: UserStore, path: RefPath, x = 0, y = 0) {
  const cells = [];
  let paramI = 0;
  let paramPath = path.concat(paramI);
  let paramKey = paramPath.join() ;
  let paramName = user.getName(paramKey);
  while (paramName != null) {
    const paramCell = {
      key: `CL-P-${paramKey}`,
      x,
      y,
      ...getTextProps(paramName || ""),
      text: paramName,
      fill: colors.param,
      color: colors.label,
      path: paramPath,
      editableName: true
    };
    cells.push(paramCell);
    x += paramCell.width || 0;
    paramI++;
    paramPath = path.concat(paramI);
    paramKey = paramPath.join() ;
    paramName = user.getName(paramKey);
  }
  return cells;
}

export function makeAllParamCells(user: UserStore, fullPath: RefPath, x = 0, y = 0) {
  const cells = [];
  let currentScopePath = fullPath;
  while (currentScopePath.length) {
    const path = currentScopePath;
    let paramI = 0;
    let paramPath = path.concat(paramI);
    let paramKey = paramPath.join() ;
    let paramName = user.getName(paramKey);
    while (paramName != null) {
      const paramCell = {
        key: `CL-P-${paramKey}`,
        x,
        y,
        ...getTextProps(paramName || ""),
        text: paramName,
        fill: colors.param,
        color: colors.label,
        path: paramPath,
        editableName: true
      };
      cells.push(paramCell);
      x += paramCell.width || 0;
      paramI++;
      paramPath = path.concat(paramI);
      paramKey = paramPath.join() ;
      paramName = user.getName(paramKey);
    }
    currentScopePath.pop();
  }
  return cells;
}



