import { isObject } from "util";

import { makeCommitLog, parseCommitLog } from "./commit";
import { InitUser, userDef, UserStore } from "./user";
import { UIStore, KeyMap, isInputKeyMap } from "./ui";
import { InitEditor, EditorStore, editorDef } from "./editor";
import { AppStore } from "./app";
import { InitRepo, isFunc, isArray, RepoStore, makeContext, repoDef, make } from "./core";
import { HashMap, safeAssign, Omit } from "./utils";

export const LOCAL_STORAGE_KEY = "LIANA";

