const defaultPayload = [CommitNode.Val, '""'];

export type CoreEditor = {
  groupFilter: string;
  editNodeVal: boolean;
  editPathName: RefPath | null;
  inLogicMenu: boolean;
  inOpMenu: boolean;
  addingNode: boolean;
  changingNode: boolean;
  insertingRefInline: boolean;
  tracePath: string[] | null;
};

export type BaseEditor = BaseUI & CoreEditor;

export type InitEditor = Partial<BaseEditor> & {
  chooser?: InitChooser;
};

export type EditorComputed = Readonly<{
  // current: EditorStore | ChooserStore;
  makeLabelCell: {
    (path: string[], x: number, y: number, groupKey?: string): Cell;
  };
  makeNodeCell: {
    (base: Cell, node: Node, index?: number, outs?: any[]): Cell;
  };
  makeNodeCells: {
    (path: string[], x: number, y: number, groupKey?: string, outs?: any[]): Cell[];
  };
  makeLineCells: {
    (path: string[], x: number, y: number, groupKey?: string, outs?: any[], refValues?: {}): Cell[];
  };
  makeDecCells: {
    (dec: FullDec, x?: number, y?: number): Cell[];
  };
  selectedSampleList: string[];
  makeSampleCells: { (x?: number, y?: number): Cell[] };
  getTrace: { (tracePath: string[], sampleIndex: number): Trace };
  makeSampleTraceCells: { (tracePath: string[], x?: number, y?: number): Cell[] };
  close: { (): void };
  closeKey: {};
  closeOnEnter: { (e: KeyboardEvent): void };
  handleChooser: { (path: RefPath): void };
  pushCommitForSelectedNode: { (payload: RefPath): void };
}>;

export type EditorStore = UIStore &
  BaseEditor &
  EditorComputed & {
    context: AppStore;
    chooser: ChooserStore;
  };

const init = (initial: InitEditor): BaseEditor => {
  const defaults: CoreEditor = {
    // TODO: better name
    addingNode: false,
    changingNode: false,
    editPathName: null,
    groupFilter: "",
    inLogicMenu: false,
    inOpMenu: false,
    tracePath: null,
    editNodeVal: false,
    insertingRefInline: false
  };
  const plainProps = safeOmit(initial, "chooser");
  const store = observable(uiDef.init(safeAssign(defaults, plainProps)));
  const chooser = make(
    chooserDef,
    safeAssign(
      initial.chooser,
      {
        get parent() {
          return store;
        }
      },
      makeContext(() => store.context)
    )
  );
  extendObservable(store, { chooser });
  return store;
};

enum Break {
  Wrap = 1,
  SpaceRight = 2
}

const noLineBreaks = (_: number, isLast = false): boolean | string | Break => false;
const standardLineBreaks = (n: number) => n > 1;
const ifLineBreaks = (n: number) => {
  switch (n) {
    case 2:
      return "then";
    case 3:
      return "else";
  }
  return false;
};
const switchLineBreaks = (n: number, isLast = false) => {
  if (n <= 1) {
    return false;
  }
  if (isLast && !(n % 2)) {
    return "else";
  }
  return n % 2 ? true : "when"; // HACK: TODO: better way than space string
};
const objLineBreaks = (n: number) => (n > 1 && n % 2 ? Break.Wrap : false);
const argLineBreaks = (_: number, isLast = false) => !isLast && Break.SpaceRight;

let parenIdCounter = 0;
const derive = (self: EditorStore) =>
  safeAssign(uiDef.derive(self), <EditorComputed>{
    pushCommitForSelectedNode(body) {
      let { path = [], index = -1 } = self.selectedCell;
      const { addingNode } = self;
      const newIndex = index + (addingNode ? 1 : 0);
      const nodePath = path.concat(newIndex);
      const kind = addingNode ? Action.InsertNode : Action.SetNode;
      const newCommit: Commit = {
        path: nodePath,
        kind,
        body
      };
      self.repo.place(newCommit);
      const newCellKey = `CL-${path.join()}-${newIndex}`;
      const newCellIndex = self.baseCells.findIndex(c => c.key === newCellKey);
      if (newCellIndex !== -1) {
        self.selectedCellIndex = newCellIndex;
      }
      self.close();
    },
    handleChooser(path) {
      if (!path) {
        self.close();
        return;
      }
      if (self.insertingRefInline) {
        self.close();
        return;
      }
      self.pushCommitForSelectedNode(path);
      self.close();
    },
    makeLabelCell(path, x, y, groupKey = "CL") {
      const { user } = self;
      let name = user.getName(path) || "";
      // if (!name) {
      //   const pathKey = path.join() ;
      //   if (pathKey.includes("_")) {
      //     name = "";
      //   } else {
      //     name = pathKey;
      //   }
      // }
      const labelCell = {
        key: `${groupKey}-${path}`,
        x,
        y,
        ...getTextProps(name),
        fill: colors.dec,
        color: colors.label,
        path,
        editableName: true
      };
      return labelCell;
    },
    makeNodeCell(baseCell, node, i = -1, outs) {
      const { user } = self;
      const cell = { ...baseCell };
      if (isVal(node as Val)) {
        cell.text = String((node as Val).val);
        cell.fill = colors.val;
        cell.width = calcWidth(cell.text || "");
      } else if (isOp(node as OpRef)) {
        const { op } = node as OpRef;
        cell.text = defaultOpNames[op] || op; // TODO: localizable op names?
        cell.fill = colors.op;
        cell.width = calcWidth(cell.text || "");
      } else if (isArg(node as Arg)) {
        const argKey = argPath(node as Arg).join();
        if (i !== -1 && outs) {
          const argValue = outs[i];
          cell.text = formatResult(argValue);
        } else {
          cell.text = user.getName(argKey);
        }
        cell.gotoCellKey = `$CL-P-${argKey}`; // TODO: better system than hard-coding
        cell.fill = colors.arg;
        cell.width = calcWidth(cell.text || "");
      } else if (isRef(node as Ref)) {
        const { ref } = node as Ref;
        const refKey = isString(ref) ? ref : ref.join(); // use ifString instead because TS wasn't inferring refKey's type correctly with isArray (which I usually use)
        const name = user.getName(refKey);
        // TODO: HACK: not sure if that last check is fully correct
        if (name) {
          cell.text = name;
        } else if (outs && i in outs) {
          const refValue = outs[i];
          if (typeof refValue !== "function") {
            cell.text = String(refValue);
          }
        }
        cell.gotoCellKey = `CL-${refKey}`; // TODO: better system than hard-coding
        cell.fill = colors.ref;
        cell.width = calcWidth(cell.text);
      }
      return cell;
    },
    makeNodeCells(path, x, y, groupKey = "CL", outs) {
      const isTraceLike = Boolean(outs);
      const pathKey = path.join();
      const { dict } = self.repo;
      const line = dict[pathKey];
      if (!line) {
        return [];
      }
      const { user } = self;
      const cells: Cell[] = [];
      let lineBreaks = noLineBreaks;
      let baseX = x;
      let connectFrom: Connector | undefined;
      let connectorCount = 0; // same...or maybe it's ok
      const pushCell = (newCell: Cell) => {
        cells.push(newCell);
        if (connectFrom) {
          connectFrom.x = newCell.x;
          connectFrom.y = newCell.y;
        }
      };
      const checkShallow = (pathKey: string, secondCheck: boolean = false): boolean => {
        // TODO: make this work for refs that start with number
        const line = dict[pathKey] || dict[pathKey + ",R"];
        if (!line) {
          return true;
        }
        for (const node of line) {
          if (isRef(node as Ref)) {
            const subRefKey = (node as Ref).ref.join();
            if (user.getName(subRefKey) == null) {
              if (secondCheck) {
                return false;
              }
              if (!checkShallow(subRefKey, true)) {
                return false;
              }
            }
          }
        }
        return true;
      };
      const [first] = line;
      for (let i = 0; i < line.length; i++) {
        const lineBreak = lineBreaks(i, i === line.length - 1);
        const node = line[i];
        const key = `${groupKey}-${path}-${i}`;
        if (lineBreak && lineBreak !== Break.SpaceRight) {
          y = cells[cells.length - 1].y + 1;
          x = baseX + 1;
          if (isString(lineBreak)) {
            const text = lineBreak || "";
            const width = Math.ceil(text.length / 5);
            cells.push({
              key: `${key}-CMNT`,
              x: x - width,
              y,
              text,
              width,
              color: colors.comment,
              display: true,
              alignRight: true
            });
          }
        }
        if (isRef(node)) {
          const { ref } = node as Ref;
          const refKey = isString(ref) ? ref : ref.join(); // use ifString instead because TS wasn't inferring refKey's type correctly with isArray (which I usually use)
          const name = user.getName(refKey);
          if (!name && !isTraceLike && !(`${refKey},R` in dict)) {
            const inlineCells = self.makeNodeCells(ref, x, y);
            const { length } = inlineCells;
            if (!length) {
              continue;
            }
            const [startCell] = inlineCells;
            const lastInlineCell = inlineCells[length - 1];
            if (lineBreak === Break.SpaceRight) {
              lastInlineCell.separateRight = true;
            }
            // find cleanest format for distinguishing cell groups
            if (startCell === lastInlineCell) {
              if (!startCell.parens) {
                startCell.parens = {};
              }
              startCell.parens[parenIdCounter] = "b";
            } else if (!lineBreak && (!cells.length || !cells[cells.length - 1].separateRight)) {
              if (!startCell.parens) {
                startCell.parens = {};
              }
              if (!lastInlineCell.parens) {
                lastInlineCell.parens = {};
              }
              startCell.parens[parenIdCounter] = "s";
              lastInlineCell.parens[parenIdCounter] = "e";
              parenIdCounter++;
            }
            if (connectFrom) {
              startCell.connectFrom = { ...connectFrom };
              connectFrom.x = startCell.x;
              connectFrom.y = startCell.y;
            }
            if (isOp(first) && first.op === Op.Object) {
              lastInlineCell.separateRight = true;
            }
            cells.push(...inlineCells);
            x = lastInlineCell.x + (lastInlineCell.width || 1);
            continue;
          }
        }
        const newCell: Cell = self.makeNodeCell(
          {
            key,
            x,
            y,
            path,
            index: i
          },
          node,
          i,
          outs
        );
        if (isOp(first) && first.op === Op.Object) {
          // TODO: replace hard-coded logic
          newCell.separateRight = true;
        }
        if (!isTraceLike) {
          // format in a readable nested format if not a trace
          if (i === 0) {
            let noSpecialCasesFound = false;
            if (isOp(first)) {
              const { op } = first;
              switch (op) {
                case Op.If:
                  newCell.separateRight = true;
                  connectFrom = { id: `${key}-${connectorCount++}`, x, y };
                  lineBreaks = ifLineBreaks;
                  break;
                case Op.Switch:
                  newCell.separateRight = true;
                  connectFrom = { id: `${key}-${connectorCount++}`, x, y };
                  lineBreaks = switchLineBreaks;
                  break;
                case Op.Object:
                  newCell.separateRight = true;
                  connectFrom = { id: `${key}-${connectorCount++}`, x, y };
                  lineBreaks = objLineBreaks;
                  break;
                default:
                  noSpecialCasesFound = true;
              }
            } else if (isArg(node) && !checkShallow(pathKey, true)) {
              newCell.separateRight = true;
              connectFrom = { id: `${key}-${connectorCount++}`, x, y };
              lineBreaks = argLineBreaks;
            } else {
              noSpecialCasesFound = true;
            }
            if (noSpecialCasesFound && !checkShallow(pathKey)) {
              newCell.separateRight = true;
              connectFrom = { id: `${key}-${connectorCount++}`, x, y };
              lineBreaks = standardLineBreaks;
            }
          }
          if (lineBreak === Break.SpaceRight) {
            newCell.separateRight = true;
          }
          if (connectFrom) {
            if (lineBreak === Break.Wrap) {
              newCell.connectFrom = { id: connectFrom.id, x: x - 0.69, y };
            } else {
              newCell.connectFrom = { ...connectFrom };
            }
          }
        }
        pushCell(newCell);
        x += newCell.width || 1;
      }
      return cells;
    },
    makeLineCells(path, x, y, groupKey = "CL", outs) {
      const cells = [];
      const lineLabelCell = self.makeLabelCell(path, x, y, groupKey);
      cells.push(lineLabelCell);
      x += lineLabelCell.width || 1;
      const nodeCells = self.makeNodeCells(path, x, y, groupKey, outs);
      cells.push(...nodeCells);
      if (!outs) {
        const pathKey = path.join();
        const line = self.repo.dict[pathKey];
        if (line) {
          const { length } = nodeCells;
          const lastNodeCell = nodeCells[length - 1];
          if (lastNodeCell) {
            const placeholderCell = {
              x: lastNodeCell.x + (lastNodeCell.width || 1),
              y: lastNodeCell.y,
              width: 0.5,
              key: `${groupKey}-${pathKey}-PLACEHOLDER`,
              path,
              index: line.length,
              fill: colors.placeholder
            };
            cells.push(placeholderCell);
          }
        }
      }
      return cells;
    },
    getTrace(tracePath, sampleIndex) {
      const { repo } = self;
      const pathKey = tracePath.join();
      const sampleList = get(repo.sampleLists, pathKey);
      if (!sampleList || sampleIndex >= sampleList.length) {
        return null;
      }
      const sampleRefKey: string | undefined = get(sampleList, sampleIndex);
      if (!sampleRefKey) {
        return null;
      }
      // if not found in the dict, it was likely deleted
      if (!repo.dict[sampleRefKey]) {
        return null;
      }
      const trace: Trace = [];
      const func = repo.gen(tracePath, trace);
      if (!isFunction(func)) {
        return trace;
      }
      const sampleValues = repo.gen(sampleRefKey);
      try {
        func(...sampleValues);
      } catch (ex) {
        if (ex instanceof TraceError) {
          return ex.trace.slice(0, 99);
        } else if (ex instanceof NodePathNotFoundError) {
          return [
            {
              key: "PATH_NOT_FOUND",
              display: true,
              ...getTextProps("Path not found!")
            }
          ];
        } else {
          throw ex;
        }
      }
      return trace;
    },
    makeSampleCells(x = 0, y = 0) {
      const { repo, tracePath } = self;
      if (!tracePath) {
        return [];
      }
      const { sampleLists } = repo;
      const sampleList = get(sampleLists, tracePath.join());
      if (!sampleList) {
        return [
          {
            x,
            y,
            key: "MAKE_SAMPLE_CELL",
            ...getTextProps("Make Sample"),
            samplePath: tracePath,
            color: colors.label,
            fill: "aquamarine"
          }
        ];
      }
      const cells: Cell[] = [];
      for (let i = 0; i < sampleList.length; i++) {
        if (!(i in sampleList)) {
          continue;
        }
        const sampleRefKey = sampleList[i];
        const sampleCells = self.makeLineCells([sampleRefKey], x, y);
        // if only one cell, it's just the label cell, implying the sample was deleted
        if (sampleCells.length < 2) {
          continue;
        }
        // HACK: remove array node
        // TODO: cleaner way to do this
        sampleCells.splice(1, 1);
        for (let i = 1; i < sampleCells.length; i++) {
          sampleCells[i].x--;
        }
        cells.push(...sampleCells);
        for (const sampleCell of sampleCells) {
          sampleCell.samplePath = tracePath;
        }
        y++;
      }
      return cells;
    },
    makeSampleTraceCells(tracePath, baseX = 0, y = 0) {
      const { user, selectedSampleList } = self;
      const cells: Cell[] = [];
      for (let sampleIndex = 0; sampleIndex < selectedSampleList.length; sampleIndex++) {
        const trace = self.getTrace(tracePath, sampleIndex);
        if (!trace) {
          continue;
        }
        const preKey = `TRC-${sampleIndex}`;
        let x = baseX;
        let refValues = {};
        for (let i = 0; i < trace.length; i++) {
          const traceLine = trace[i];
          const { path, args, outs = [], result, closing } = traceLine;
          const pathKey = asKey(path);
          if (result != null) {
            refValues = { ...refValues, [pathKey]: result };
          }
          if (path.length < 2) {
            continue;
          }
          const hasResult = traceLineResultKey in traceLine;
          // TODO: when ready, uncomment following logic to "collapse" nested calls
          // if (x > baseX + 2) {
          //   if (args && !hasResult) {
          //     x++;
          //   }
          //   if (args && hasResult) {
          //     x--;
          //   }
          //   continue;
          // }
          if (!closing) {
            let indentX = baseX;
            while (indentX < x) {
              cells.push({
                x: indentX,
                y,
                key: `INDENT-${indentX}-${y}`,
                leftGridLine: true,
                display: true
              });
              indentX++;
            }
          }
          if (args && !hasResult) {
            const scopePath = path.slice(0, -1);
            let subX = x;
            const labelCell = self.makeLabelCell(scopePath, subX, y, `${preKey}-LBL-${y}`);
            cells.push(labelCell);
            subX += labelCell.width || 1;
            for (let j = 0; j < args.length; j++) {
              const value = args[j];
              const argName = user.getName([...scopePath, String(j)]);
              const valText = formatResult(value);
              const text = argName ? `${argName} = ${valText}` : valText;
              const valueCell = {
                x: subX,
                y,
                key: `${preKey}-ARG-${i}-${j}`, // TODO: non-position based key
                text,
                width: calcWidth(text),
                fill: colors.param,
                color: colors.label
              };
              cells.push(valueCell);
              subX += valueCell.width || 1;
            }
            y++;
            x++;
            continue;
          }
          if (hasResult && !closing) {
            const lineCells = self.makeLineCells(
              path,
              x,
              y,
              `${preKey}-${pathKey}-${i}`,
              outs,
              refValues
            );
            cells.push(...lineCells);
            const lastCell = lineCells[lineCells.length - 1];
            cells.push({
              x: lastCell.x + (lastCell.width || 1),
              y,
              key: `${preKey}-${pathKey}-${i}-RES`,
              ...getTextProps(result),
              display: true
            });
            y++;
          }
          if (closing) {
            x--;
          }
        }
        y++;
      }
      return cells;
    },
    makeDecCells(dec, x = 0, y = 0): Cell[] {
      const { line, path } = dec;
      if (!line.length) {
        return [];
      }
      const cells: Cell[] = [];
      const isChild = Boolean(path.length);
      const { user } = self;
      if (isChild) {
        const isDecList = isFullDecList(line);
        if (!isDecList) {
          const lineCells = self.makeLineCells(path, x, y);
          cells.push(...lineCells);
          if (path.length > 1) {
            return cells;
          }
          const lastLineCell = lineCells[lineCells.length - 1];
          x = lastLineCell.x + (lastLineCell.width || 1);
          y = lastLineCell.y + (lastLineCell.height || 0);
          const result = self.repo.gen(path);
          const resultCell: Cell = {
            x,
            y,
            key: `CL-RES-${path}`,
            ...getTextProps(result),
            display: true
          };
          cells.push(resultCell);
          return cells;
        }
        const decLabelCell = self.makeLabelCell(path, x, y);
        cells.push(decLabelCell);
        const paramX = x + (decLabelCell.width || 1);
        const paramCells = makeParamCells(user, path, paramX, y);
        cells.push(...paramCells);
        const castPath: RefPath = path;
        const prevCell = cells[cells.length - 1];
        const slotPath = castPath.concat(paramCells.length);
        cells.push({
          key: `CL-SLOT-${slotPath}`,
          x: prevCell ? prevCell.x + (prevCell.width || 1) : x,
          y,
          width: 0.5,
          fill: colors.placeholder,
          path: slotPath,
          editableName: true
        });
        y++;
      }
      let lastSubDecCells;
      for (let i = 0; i < line.length; i++) {
        const subDec = line[i] as FullDec;
        // skip if detected to be anonymous inline line
        if (subDec.path[subDec.path.length - 1].startsWith("_")) {
          continue;
        }
        const subX = path.length ? x + 1 : x;
        const subDecCells = self.makeDecCells(subDec, subX, y);
        lastSubDecCells = subDecCells;
        const { length } = subDecCells;
        if (length) {
          const lastNewCell = subDecCells[length - 1];
          y = lastNewCell.y + 1;
        }
        cells.push(...subDecCells);
        if (!isChild) {
          y++;
        }
      }
      if (isChild && lastSubDecCells) {
        const subLabelCell = lastSubDecCells[0];
        if (!subLabelCell.text) {
          subLabelCell.text = "←";
        }
      }
      return cells;
    },
    close() {
      self.addingNode = false;
      self.changingNode = false;
      self.inLogicMenu = false;
      self.inOpMenu = false;
      self.editNodeVal = false;
      self.editPathName = null;
    },
    closeOnEnter(e) {
      if (e.keyCode === 13) {
        self.close();
      }
    },
    get closeKey() {
      return { label: "Cancel", action: self.close };
    },
    get selectedSampleList() {
      const { tracePath } = self;
      if (!tracePath) {
        return [];
      }
      return get(self.repo.sampleLists, tracePath.join()) || [];
    },
    get baseCells() {
      const { repo, tracePath } = self;
      if (tracePath) {
        const tracePathKey = tracePath.join();
        const topDec = repo.topMap[tracePathKey];
        if (topDec) {
          const mainCells = self.makeDecCells(topDec);
          const lastMainCell = mainCells[mainCells.length - 1];
          let rightX = 0;
          for (const cell of mainCells) {
            const { x } = cell;
            if (x >= rightX) {
              rightX = x + (cell.width || 1);
            }
          }
          const sampleCells = self.makeSampleCells(0, lastMainCell.y + 2);
          const sampleTraceCells = self.makeSampleTraceCells(tracePath, rightX + 1);
          return mainCells.concat(sampleCells, sampleTraceCells);
        }
      }
      return self.makeDecCells(repo.full);
    },
    get focusedStore() {
      if (self.chooser.forPath) {
        return self.chooser;
      }
      return self;
    },
    get activeCells() {
      const { cursorCell } = self;
      if (self.chooser.forPath) {
        const chooserGroupCell = {
          key: "CHSR_GRP",
          x: cursorCell.x,
          y: cursorCell.y + 1,
          cells: self.chooser.cells
        };
        return self.cells.concat(chooserGroupCell);
      }
      return self.cells;
    },
    get input() {
      if (self.chooser.input != null) {
        return self.chooser.input;
      }
      if (self.editNodeVal) {
        const { path, index } = self.selectedCell;
        if (!path || index == null) {
          return;
        }
        const node = self.repo.dict[path.join()][index];
        return isVal(node) ? node.val : "";
      }
      if (self.editPathName) {
        return self.user.getName(self.editPathName.join()) || "";
      }
      return null;
    },
    handleInput(e: any) {
      if (self.chooser.forPath) {
        return self.chooser.handleInput(e);
      }
      const { value } = e.target as HTMLInputElement;
      if (self.editPathName != null) {
        const pathKey = self.editPathName.join();
        set(self.user.nameSet, pathKey, value);
        return;
      }
      if (self.editNodeVal) {
        const { path, index } = self.selectedCell;
        if (!path || index == null) {
          return;
        }
        const { repo } = self;
        const pathKey = path.join();
        const { val } = repo.dict[pathKey][index] as Val;
        const body: string[] = [CommitNode.Val];
        if (isNumber(val)) {
          const nextValAsNumber = Number(value);
          if (Number.isNaN(nextValAsNumber)) {
            return;
          }
          body[1] = String(nextValAsNumber);
        } else if (isString(val)) {
          body[1] = JSON.stringify(value);
        }
        const { commits } = repo;
        const lastCommit = commits[commits.length - 1];
        const lastCommitPath = lastCommit.path.slice(0, -1);
        const lastCommitPathKey = lastCommitPath.join();
        if (lastCommitPathKey === pathKey) {
          lastCommit.body = body;
        } else {
          commits.push({ path: path.concat(index), kind: Action.SetNode, body });
        }
      }
    },
    get keyMap() {
      if (self.chooser.forPath) {
        return self.chooser.keyMap;
      }
      if (self.editNodeVal) {
        return <InputKeyMap>{
          title: "Type the New Value",
          enter: "Return",
          onKey: self.closeOnEnter
        };
      }
      const { selectedCell } = self;
      if (!selectedCell) {
        return {};
      }
      const { path } = selectedCell;
      if (self.inLogicMenu || self.inOpMenu) {
        const o = (op: Op) => ({
          label: defaultOpNames[op],
          action() {
            self.pushCommitForSelectedNode([CommitNode.Op, op]);
          }
        });
        if (self.inLogicMenu) {
          return {
            1: {
              0: o(Op.LessThan),
              1: o(Op.LessOrEq),
              2: o(Op.GreaterOrEq),
              3: o(Op.GreaterThan)
            },
            2: {
              0: o(Op.StrictEq),
              1: o(Op.Equal),
              2: o(Op.StrictNotEq),
              3: o(Op.NotEq),
              6: o(Op.If),
              7: o(Op.Switch),
              8: o(Op.And),
              9: o(Op.Or)
              // 8: o(Op.TypeOf),
              // 9: o(Op.InstanceOf),
              // 9: o(Op.While)
            },
            3: { 9: self.closeKey }
          };
        }
        return {
          1: {
            5: o(Op.Add),
            6: o(Op.Minus),
            7: o(Op.Times),
            8: o(Op.Div),
            9: o(Op.Mod)
          },
          2: {
            0: o(Op.Global),
            1: o(Op.Dot),
            2: o(Op.This),
            3: o(Op.Undef),
            4: o(Op.Mutate),
            6: o(Op.Array),
            7: o(Op.Object),
            8: o(Op.New),
            9: o(Op.Class)
          },
          3: { 9: self.closeKey }
        };
      }

      const addLine = (path: RefPath = [], payload = defaultPayload, inline = false) => {
        const parentScopePath = path.slice(0, -1);
        let parentScope = self.repo.packed;
        for (const id of parentScopePath) {
          parentScope = parentScope[id];
        }
        const scopeIds = Object.keys(parentScope);
        const lastScopeId = scopeIds[scopeIds.length - 1];
        let newLineId = nextLetterId(lastScopeId, inline);
        while (parentScope[newLineId]) {
          newLineId = nextLetterId(newLineId, inline);
        }
        const newPath = parentScopePath.concat(newLineId);
        const nodePath = newPath.concat(0);
        const placement = {
          kind: Action.SetNode,
          path: nodePath,
          body: payload
        };
        self.repo.place(placement);
        return newPath;
      };

      if (self.addingNode || self.changingNode) {
        const setVal = (val: string) => self.pushCommitForSelectedNode([CommitNode.Val, val]);
        const nodeMenuKeyMap = {
          1: {
            0: {
              label: "True",
              action: () => setVal("true")
            },
            1: {
              label: "False",
              action: () => {
                setVal("false");
              }
            },
            2: {
              label: "Number",
              action: () => {
                setVal("0");
                self.editNodeVal = true;
              }
            },
            3: {
              label: "String",
              action: () => {
                setVal('""');
                self.editNodeVal = true;
              }
            }
          },
          2: {
            2: {
              label: "Op",
              action() {
                self.inOpMenu = true;
              }
            },
            3: {
              label: "Logic",
              action() {
                self.inLogicMenu = true;
              }
            }
          },
          3: { 9: self.closeKey }
        };
        if (path) {
          nodeMenuKeyMap[2][1] = {
            label: "Inline",
            action: action(() => {
              const { index } = selectedCell;
              if (index == null) {
                return;
              }
              const newPath = addLine(path, undefined, true);
              if (!newPath) {
                return;
              }
              const newLineId = newPath[newPath.length - 1];
              const body = [CommitNode.Ref, String(newLineId)];
              self.pushCommitForSelectedNode(body);
              // TODO: more robust way to find created cell (same for "Add Scope")
              const newCellIndex = self.baseCells.findIndex(
                c => c.key === `CL-${newPath.join()}-0`
              );
              self.selectedCellIndex = newCellIndex;
            })
          };
          nodeMenuKeyMap[2][6] = {
            label: "Choose Ref",
            action() {
              self.chooser.forPath = path;
            }
          };
          nodeMenuKeyMap[2][7] = {
            label: "Copy Ref Inline",
            action: action(() => {
              self.insertingRefInline = true;
              self.chooser.forPath = path;
            })
          };
        }
        return nodeMenuKeyMap;
      }
      if (self.editPathName) {
        return {
          title: "Type to Change Name",
          enter: "Save Name",
          esc: "Cancel",
          tab: "Save Name & Move Right",
          onKey: self.closeOnEnter
        };
      }
      const keyMap = {
        1: {
          0: {
            label: "Save",
            action() {
              const snapshot = packApp(toJS(self.context));
              if (process.env.NODE_ENV === "production") {
                const serialized = JSON.stringify(snapshot);
                localStorage.setItem(LOCAL_STORAGE_KEY, serialized);
              }
              const { repo = {} } = snapshot;
              if (!repo) {
                return;
              }
              const withoutCommits = assocIn(
                snapshot,
                pathOf(snapshot)(x => x.repo!.commits),
                null
              );
              // pardon prettier's awful formatting of this templated string
              const code = `
            import { PackedApp } from "./app";

            export const commits = \`${repo.commits}\`;
            \nexport const snapshot : PackedApp = ${JSON.stringify(withoutCommits).replace(
              '"commits":null',
              "commits"
            )}
            \nexport default snapshot;
            `;
              console.info(code);
              if (process.env.NODE_ENV === "development") {
                // if possible, copy to clipboard for testing/debugging
                if (!copy) {
                  return;
                }
                copy(code);
              }
            }
          }
        },
        2: {},
        3: {
          5: {
            label: "Reset",
            action() {
              self.tracePath = null;
              const nameOfRepoProp = nameOf(self.repo);
              const commitProp = nameOfRepoProp(x => x.commits);
              const sampleListsProp = nameOfRepoProp(x => x.sampleLists);
              set(self.repo, commitProp, [
                { path: ["a", 0], kind: "n", body: ["o", "+"] },
                { path: ["a", 1], kind: "n", body: ["v", "1"] },
                { path: ["a", 2], kind: "n", body: ["v", "2"] }
              ]);
              set(self.repo, sampleListsProp, {});
              const nameSetsProp = nameOf(self.user)(x => x.nameSets);
              set(self.user, nameSetsProp, { "en-US": { a: "sum" } });
            }
          }
        }
      };
      if (process.env.NODE_ENV === "development") {
        keyMap[1][1] = {
          label: "Debug",
          action() {
            (print as any)(selectedCell);
          }
        };
      }
      // if (path && selectedCell.isLine) {
      //   // TODO: no longer working? revisit this eventually
      //   keyMap[1][8] = {
      //     label: "Clone",
      //     action() {
      //       const { dict, commits } = self.repo;
      //       const lastId = path[path.length - 1];
      //       const scopePath = path.slice(0, -1);
      //       let cloneId = `${lastId}.1`;
      //       let nextKey = scopePath.concat(cloneId).join() ;
      //       while (nextKey in dict) {
      //         cloneId = nextLetterId(cloneId);
      //         nextKey = scopePath.concat(cloneId).join() ;
      //       }
      //       const clonePath = scopePath.concat(cloneId);
      //       const clonePathKey = clonePath.join() ;
      //       set(commits, clonePathKey, [Action.CloneRef, lastId]);
      //     }
      //   };
      // }
      if (path) {
        keyMap[1][8] = {
          label: "Add Node",
          action() {
            self.addingNode = true;
          }
        };
        keyMap[2][6] = {
          label: "Change Node",
          action() {
            self.changingNode = true;
          }
        };
      }

      if (path) {
        if (self.tracePath) {
          keyMap[2][4] = {
            label: "Close Edit",
            action() {
              self.tracePath = null;
            }
          };
        } else if (path.length < 2 && self.repo.dict[path.concat("R").join()]) {
          keyMap[2][4] = {
            label: "Live Edit",
            action() {
              self.tracePath = path.map(String);
              self.selectedCellIndex = 0;
            }
          };
        }
        keyMap[1][6] = {
          label: "Add Function",
          action() {
            const parentScopePath = path.slice(0, -1);
            let parentScope = self.repo.packed;
            for (const id of parentScopePath) {
              parentScope = parentScope[id];
            }
            let currentLineId = "a";
            while (currentLineId in parentScope) {
              currentLineId = nextLetterId(currentLineId);
            }
            const newScopePath = parentScopePath.concat(currentLineId);
            const nodePath = newScopePath.concat("a", 0);
            const body = defaultPayload;
            const placement: Commit = {
              kind: Action.SetNode,
              path: nodePath,
              body
            };
            const newScopeKey = newScopePath.join();
            set(self.user.nameSet, {});
            self.repo.place(placement);
            self.editPathName = newScopePath;
            const newCellIndex = self.baseCells.findIndex(
              (c: any) => c.key === `CL-${newScopeKey}`
            );
            self.selectedCellIndex = newCellIndex;
          }
        };
        keyMap[1][7] = {
          label: "Add Line",
          action() {
            const newPath = addLine(path);
            if (!newPath) {
              return;
            }
            const newPathKey = newPath.join(); // TODO: one of those cases where we're recalculating something we already calculated (food for thought on caching system...)
            self.editPathName = newPath;
            // TODO: more robust way to find created cell (same for "Add Scope")
            const newCellIndex = self.baseCells.findIndex((c: any) => c.key === `CL-${newPathKey}`);
            self.selectedCellIndex = newCellIndex;
          }
        };
      }
      const { samplePath } = selectedCell;
      if (samplePath) {
        keyMap[3][4] = {
          label: "Add Sample",
          action() {
            const [newId] = addLine(undefined, [CommitNode.Op, Op.Array], true);
            const { sampleLists } = self.repo;
            const samplePathKey = samplePath.join();
            if (!has(sampleLists, samplePathKey)) {
              set(sampleLists, { [samplePathKey]: [] });
            }
            const newIdAsString = String(newId);
            const selectedSampleList = sampleLists[samplePathKey];
            if (selectedSampleList.indexOf(newIdAsString) === -1) {
              selectedSampleList.push(newIdAsString);
            }
          }
        };
      }
      // TODO: re-implement Delete action
      // if (path && index != null) {
      // keyMap[3][9] = {
      //   label: "Delete",
      //   action() {
      //     self.repo.commits.push({
      //       path: path.concat(index),
      //       kind: Action.RemoveNode
      //     });
      //   }
      // };
      // }
      if (selectedCell.editableName) {
        keyMap[2][6] = {
          label: "Change Name",
          action() {
            self.editPathName = selectedCell.path || null;
          }
        };
      }
      if (selectedCell.gotoCellKey) {
        keyMap[2][7] = {
          label: "Go To Def",
          action() {
            const gotoCellIndex = self.baseCells.findIndex(
              (cell: Cell) => cell.key === selectedCell.gotoCellKey
            );
            if (gotoCellIndex !== -1) {
              self.selectedCellIndex = gotoCellIndex;
            }
          }
        };
      }
      return defaultsDeep(keyMap, self.baseKeyMap);
    }
  });

export const editorDef = {
  init,
  derive
};

/********** Utility Functions **********/

export function nextLetterId(prev: string, inline = false) {
  const isInline = prev.startsWith("_");
  const main = isInline ? prev.slice(1) : prev;
  const next = main.split("");
  const addAtIndex = (index: number) => {
    const val = main[index];
    if (val === "z") {
      next[String(index)] = 0;
      if (index === 0) {
        next.splice(0, 0, "a");
      } else {
        addAtIndex(index - 1);
      }
    } else {
      const valAsInt = parseInt(val, 36);
      next[index] = (valAsInt + 1).toString(36);
    }
  };
  addAtIndex(main.length - 1);
  const nextMain = next.join("");
  return inline ? `_${nextMain}` : nextMain;
}



