import { cloneDeep } from "lodash";
import { assocIn, getIn, splice, updateIn } from "icepick";

import { RefPath, PackedNode, Op } from "./core";

export enum Action {
  Skip = "",
  AddScope = "s",
  AddLine = "l",
  SetNode = "n",
  InsertNode = "i",
  RemoveNode = "r",
  CloneRef = "c"
}

export enum CommitNode {
  Val = "v",
  Op = "o",
  Ref = "r",
  Arg = "a"
}

export type Commit = {
  path: RefPath;
  kind: string;
  body?: RefPath;
};

export function parseCommitLog(log: string): Commit[] {
  const messages = log.split("\n");
  const commits = [];
  for (const msg of messages) {
    const splitIndex = msg.indexOf("/");
    const rawPlacement = msg.substring(0, splitIndex);
    const body = msg.substring(splitIndex + 1);
    if (!rawPlacement) {
      continue;
    }
    const [kind, ...path] = rawPlacement.split("'");
    const lastPathIndex = path.length - 1;
    // TODO: maybe improve this hacky-feeling number check
    const lastPathIdAsNum = Number(path[lastPathIndex]);
    if (!Number.isNaN(lastPathIdAsNum)) {
      path[String(lastPathIndex)] = lastPathIdAsNum;
    }
    const placement = { path, kind, body: body.split("'") };
    commits.push(placement);
  }
  return commits;
}

export function processCommits(commits: Commit[], main: object = {}) {
  for (const placement of commits) {
    main = processCommit(placement, main);
  }
  return main;
}

export function processCommit(placement: Commit, main: object = {}) {
  const { path, kind, body } = placement;
  switch (kind) {
    case Action.Skip:
      return main;
    case Action.SetNode:
    case Action.InsertNode: {
      if (!body) {
        return main;
      }
      const linePath = path.slice(0, -1);
      const currentLine = getIn(main, linePath);
      if (!currentLine) {
        main = assocIn(main, linePath, []);
      }
      const [nodeKind, ...nodeValue] = body;
      let newNode: PackedNode;
      switch (nodeKind) {
        case CommitNode.Val:
          let [rawVal] = nodeValue;
          newNode = [JSON.parse(rawVal as string)];
          break;
        case CommitNode.Op:
          const [op] = nodeValue;
          newNode = (op as string).toUpperCase() as Op;
          break;
        case CommitNode.Ref:
          if (nodeValue.length === 1) {
            newNode = { r: nodeValue };
          } else {
            const [levelsUp, id] = nodeValue;
            newNode = { r: [Number(levelsUp), id] };
          }
          break;
        case CommitNode.Arg:
          if (nodeValue.length === 1) {
            newNode = { a: nodeValue };
          } else {
            newNode = { a: nodeValue.map(Number) };
          }
          break;
        default:
          throw new Error(`Invalid Commit Node Type: ${nodeKind}`);
      }

      switch (kind) {
        case Action.SetNode:
          main = assocIn(main, path, newNode);
          break;
        case Action.InsertNode:
          const linePath = path.slice(0, -1);
          const index = Number(path[path.length - 1]);
          main = updateIn(main, linePath, (line: PackedNode[]) => splice(line, index, 0, newNode));
      }
      return main;
    }
    case Action.RemoveNode: {
      const linePath = path.slice(0, -1);
      const index = Number(path[path.length - 1]);
      main = updateIn(main, linePath, (line: PackedNode[]) => splice(line, index, 1));
      return main;
    }
    case Action.CloneRef: {
      if (!body) {
        return main;
      }
      const [fromSiblingId] = body;
      const sourcePath = path.slice(0, -1).concat(fromSiblingId);
      const source = getIn(main, sourcePath);
      main = assocIn(main, path, cloneDeep(source));
      return main;
    }
  }
  return main;
}

export function makeCommitLog(commits: Commit[]) {
  let log = "";
  for (const commit of commits) {
    log += "\n";
    const { path, kind, body } = commit;
    const first = [kind].concat(path.map(String)).join("'");
    if (body) {
      const second = body.join("'");
      const line = `${first}/${second}`;
      log += line;
    } else {
      log += first;
    }
  }
  return log;
}



