import { keys } from "mobx";
import { remove } from "lodash";

const { defineProperty, getOwnPropertyDescriptor } = Object;

// safeAssign is designed to work like Object.assign or the spread operator
// but WITHOUT running getters, which I often design to be lazy
// it copies the getter itself, which is pretty much always what I want
export function safeAssign(target: any = {}, ...more: any[]) {
  const assign = (newProps: any) => {
    if (!newProps) {
      return;
    }
    if (typeof newProps === "function") {
      newProps = newProps(target);
    }
    const newPropKeys = Object.keys(newProps);
    for (const propKey of newPropKeys) {
      const newProp = getOwnPropertyDescriptor(newProps, propKey);
      if (newProp) {
        defineProperty(target, propKey, newProp);
      }
    }
  };
  for (const moreProps of more) {
    assign(moreProps);
  }
  return target;
}

export function safeOmit<T>(target: T, ...omitProps: (keyof T)[]) {
  const copy = {};
  for (const propKey in target) {
    if (omitProps.indexOf(propKey) !== -1) {
      continue;
    }
    const newProp = getOwnPropertyDescriptor(target, propKey);
    if (newProp) {
      defineProperty(copy, propKey, newProp);
    }
  }
  return copy;
}

export type HashMap<T> = {
  [id: string]: T;
};

export type Diff<T extends string, U extends string> = ({ [P in T]: P } &
  { [P in U]: never } &
  HashMap<never>)[T];

export type Omit<T, K extends keyof T> = Pick<T, Diff<keyof T, K>>;

export function nameOf<T>(obj: T) {
  let name: string;
  const makeCopy = (obj: any): any => {
    const copy = {};
    for (const key in obj) {
      defineProperty(copy, key, {
        get() {
          name = key;
          const value = obj[key];
          if (value && typeof value === "object") {
            return makeCopy(value);
          }
          return value;
        }
      });
    }
    return copy;
  };
  return (accessor: { (x: T): any }): string => {
    name = "";
    accessor(makeCopy(obj));
    return name;
  };
}

export function pathOf<T>(obj: T) {
  let path: string[] = [];
  const makeCopy = (obj: any): any => {
    const copy = {};
    for (const key in obj) {
      defineProperty(copy, key, {
        get() {
          path.push(key);
          const value = obj[key];
          if (value && typeof value === "object") {
            return makeCopy(value);
          }
          return value;
        }
      });
    }
    return copy;
  };
  return (accessor: { (x: T): any }): string[] => {
    path = [];
    accessor(makeCopy(obj));
    return path;
  };
}

// remove all entries from observable object
export function removeAll(obj: any) {
  const objKeys = keys(obj);
  for (const key of objKeys) {
    remove(obj, key);
  }
}



