import { observable, isObservable, extendObservable } from "mobx";
import { memoize } from "lodash";
import { isString, isObject, isError, isBoolean, isNumber } from "util";

import { RepoComputed } from "./core";
import { Commit, processCommits, CommitNode } from "./commit";
import { HashMap, nameOf, safeAssign } from "./utils";
import { assoc } from "icepick";

const maxScopes = 9;

export const makeContext = <T>(getContext: { (): T }) => ({
  get context() {
    return getContext();
  }
});

export function make(def: any, initial: any) {
  const props = def.init(initial);
  const store = isObservable(props) ? props : observable(props);
  const derived = def.derive(store);
  extendObservable(store, derived);
  return store;
}

export function isFunc(val: any): val is Function {
  return val instanceof Function;
}

export function isArray(val: any): val is Array<any> {
  return val instanceof Array;
}

// TODO: Regex, other types?
export type Val = {
  val?: string | number | boolean;
};

export enum Op {
  // Control Flow
  If = "?",
  Switch = "S",
  For = "F",
  While = "L",
  And = "A",
  Or = "O",
  // Comparison
  Equal = "=",
  StrictEq = "E",
  NotEq = "!",
  StrictNotEq = "N",
  LessThan = "<",
  LessOrEq = "V",
  GreaterThan = ">",
  GreaterOrEq = "^",
  TypeOf = "T",
  InstanceOf = "I",
  // Math
  Add = "+",
  Minus = "-",
  Times = "*",
  Div = "/",
  Mod = "%",
  // Access
  Global = "G",
  Dot = ".",
  This = "H",
  Undef = "U",
  // Define, Create, Set
  Array = "[",
  Object = "{",
  Class = "C",
  New = "W",
  Mutate = "@"
  // TODO: Bitwise Ops
}

export type OpRef = {
  op: Op;
};

function throwSpecialCaseOp() {
  throw new Error("Special-cased op...this shouldn't be run!");
}

function throwUnimplementedOp() {
  throw new Error("This op has not been implemented yet!");
}

const opFuncs = Object.freeze({
  [Op.If]: throwSpecialCaseOp,
  [Op.Switch]: throwSpecialCaseOp,
  [Op.For]: throwUnimplementedOp,
  [Op.While]: throwUnimplementedOp,
  [Op.And]: throwSpecialCaseOp,
  [Op.Or]: throwSpecialCaseOp,
  [Op.Equal](a: any, b: any) {
    return a == b; // tslint:disable-line
  },
  [Op.StrictEq](a: any, b: any) {
    return a === b;
  },
  [Op.NotEq](a: any, b: any) {
    return a != b; // tslint:disable-line
  },
  [Op.StrictNotEq](a: any, b: any) {
    return a !== b;
  },
  [Op.LessThan](a: any, b: any) {
    return a < b;
  },
  [Op.LessOrEq](a: any, b: any) {
    return a <= b;
  },
  [Op.GreaterThan](a: any, b: any) {
    return a > b;
  },
  [Op.GreaterOrEq](a: any, b: any) {
    return a >= b;
  },
  [Op.Add](...nums: number[]) {
    let sum = nums[0];
    for (let i = 1; i < nums.length; i++) {
      sum += nums[i];
    }
    return sum;
  },
  [Op.Minus](a: number, b: number) {
    return a - b;
  },
  [Op.Times](...nums: number[]) {
    let product = nums[0];
    for (let i = 1; i < nums.length; i++) {
      product *= nums[i];
    }
    return product;
  },
  [Op.Div](a: number, b: number) {
    return a / b;
  },
  [Op.Mod](a: number, b: number) {
    return a % b;
  },
  [Op.Global]: typeof window !== "undefined" ? window : global,
  [Op.Dot](obj: any, key: any) {
    return obj[key];
  },
  [Op.This]: throwUnimplementedOp, // TODO: probably as a special case op
  [Op.Undef]: undefined,
  [Op.Array](...items: any[]) {
    return items;
  },
  [Op.Object](...kvs: any[]) {
    const obj = {};
    for (let i = 0; i < kvs.length; i = i + 2) {
      obj[kvs[i]] = kvs[i + 1];
    }
    return obj;
  },
  [Op.Class]: throwUnimplementedOp,
  [Op.New](Class: any, ...args: any[]) {
    return new Class(...args);
  },
  [Op.Mutate]: throwUnimplementedOp
});

const ops = [];
for (const op in opFuncs) {
  ops.push(op);
}
Object.freeze(ops);

export const defaultOpNames = Object.freeze({
  [Op.If]: "IF",
  [Op.Switch]: "CASE",
  [Op.And]: "&&",
  [Op.Or]: "||",
  [Op.Equal]: "==",
  [Op.StrictEq]: "===",
  [Op.NotEq]: "!=",
  [Op.StrictNotEq]: "!==",
  [Op.LessThan]: "<",
  [Op.LessOrEq]: "<=",
  [Op.GreaterThan]: ">",
  [Op.GreaterOrEq]: ">=",
  [Op.Add]: "+",
  [Op.Minus]: "-",
  [Op.Times]: "×",
  [Op.Div]: "/",
  [Op.Mod]: "%",
  [Op.Global]: "🌐",
  [Op.Dot]: "•",
  [Op.This]: "THIS",
  [Op.Undef]: "U",
  [Op.Array]: "[ ]",
  [Op.Object]: "{ }",
  [Op.New]: "NEW",
  [Op.Mutate]: "SET!"
});

export type DecPath = string[];
export type DecId = DecPath | string;
export type RefPath = (string | number)[]; // if it ends with number, it points to an arg in scope

export type Pkg = {
  id: string;
};

export type PkgRef = {
  pkg: string;
};

export type Arg = {
  scope: RefPath;
  arg?: number;
};

export type Ref = {
  ref: string[];
};

export type Node = Val | OpRef | PkgRef | Arg | Ref;

export function isVal(node: Node): node is Val {
  if (node == null) {
    return false;
  }
  return (node as Val).val !== undefined;
}
export function isOp(node: Node): node is OpRef {
  if (node == null) {
    return false;
  }
  return (node as OpRef).op != null;
}
export function isArg(node: Node): node is Arg {
  if (node == null) {
    return false;
  }
  return (node as Arg).scope != null;
}
export function isRef(node: Node): node is Ref {
  if (node == null) {
    return false;
  }
  return (node as Ref).ref != null;
}

export function argPath(node: Arg): RefPath {
  const { scope, arg = 0 } = node;
  if (isArray(scope)) {
    return scope.concat(arg);
  }
  return [scope, arg];
}

export type Line = Node[] | Dec[];

export type Dec = {
  id: string;
  line: Line;
};

export type FullLine = FullDec[] | Node[];
export type FullDec = {
  id: string;
  path: DecPath;
  line: FullLine;
};

export type DecDict = HashMap<Node[]>;

export function isDec(node: Node | Dec): node is Dec {
  if (!node) {
    return false;
  }
  return (node as Dec).line != null;
}

export function isDecList(line: Line): line is Dec[] {
  if (!line || !line.length) {
    return false;
  }
  const [first] = line;
  if (!first || !isObject(first)) {
    return false;
  }
  return isDec(first);
}

export function isFullDec(node: Node | FullDec): node is FullDec {
  if (!node) {
    return false;
  }
  return (node as FullDec).line != null;
}

export function isFullDecList(line: FullLine): line is FullDec[] {
  if (!line || !line.length) {
    return false;
  }
  const [first] = line;
  if (!first || !isObject(first)) {
    return false;
  }
  return isFullDec(first);
}

export type TraceLine = {
  path: string[];
  args?: any[];
  outs?: any[];
  result?: any;
  closing?: boolean;
};

export const getTraceLinePropKey = nameOf(<TraceLine>{ result: 1 });
export const traceLineResultKey = getTraceLinePropKey(x => x.result);

export type ArgStack = any[][];

export type Trace = TraceLine[] & {
  argStack?: ArgStack;
};

const parseNode = (repoDict: DecDict, node: Node, trace?: Trace, scopes: object = {}): any => {
  if (isVal(node)) {
    return node.val;
  }
  if (isOp(node)) {
    return opFuncs[node.op];
  }
  if (isRef(node)) {
    const result = gen(repoDict, node.ref, trace, scopes);
    return result;
  }
  if (isArg(node)) {
    const { scope, arg } = node;
    const scopeKey = isArray(scope) ? scope.join()  : scope;
    const scopeArgs = scopes[scopeKey];
    if (!scopeArgs) {
      return; // TODO: fill in defaults (or some other behavior?)
    }
    return scopeArgs[arg || 0];
  }
  if (!node) {
    return new Error("Missing Node"); // TODO: revisit this method of handling (too loose? can it be refined?)
  }
  throw new Error(`Invalid node: ${JSON.stringify(node)}`);
};

export class TraceError {
  trace: Trace = [];
  constructor(trace: Trace) {
    this.trace = trace;
  }
}

export class NodePathNotFoundError extends Error {}

export function gen(repoDict: DecDict, path: DecId, trace?: Trace, scopes: object = {}): any {
  if (!isArray(path)) {
    path = [path];
  }
  const decKey = path.join() ;
  const line: Line = repoDict[decKey];
  if (!line) {
    if (path.length > maxScopes) {
      throw new NodePathNotFoundError(`Node path not found: ${path.slice(0, 9)}`);
    }
    const newScopes = { ...scopes };
    delete newScopes[decKey]; // TODO: do we have to delete all subscopes? need to test more varied nesting cases
    return gen(repoDict, path.concat("R"), trace, newScopes);
  }
  const { length } = path;
  if (length > 1) {
    let scopeLevel = 0;
    let currentScopePath = [];
    let currentScopeKey = "";
    let currentScope;
    do {
      currentScopePath.push(path[scopeLevel]);
      currentScopeKey = currentScopePath.join() ;
      currentScope = scopes[currentScopeKey];
      scopeLevel++;
    } while (currentScope && scopeLevel < length - 1);
    if (!currentScope) {
      return function(...args: any[]) {
        const newScopes = { ...scopes, [currentScopeKey]: args };
        if (trace) {
          trace.push({
            path: asArray(path),
            args
          });
        }
        const result = gen(repoDict, path, trace, newScopes);
        if (trace) {
          trace.push({
            path: asArray(path),
            args,
            result,
            closing: true
          });
        }
        return result;
      };
    }
  }
  const parseRepoNode = (node: Node): any => parseNode(repoDict, node, trace, scopes);
  const [first] = line;
  let result;
  let noSpecialCasesFound = false;
  let outs: any[] = []; // TODO: make optional?
  if (isOp(first)) {
    switch (first.op) {
      case Op.If:
        outs.push(undefined);
        const cond = parseRepoNode(line[1]);
        outs.push(cond);
        if (isError(cond)) {
          result = cond;
          break;
        }
        if (cond) {
          result = parseRepoNode(line[2]);
          outs.push(result);
          break;
        }
        // TODO: if node at 3 not found, auto-return case expression? return false?
        result = parseRepoNode(line[3]);
        outs.push(undefined, result);
        break;
      case Op.And:
        let andResult;
        for (let i = 1; i < line.length; i++) {
          andResult = parseRepoNode(line[i]);
          if (!andResult || isError(andResult)) {
            result = andResult;
            break;
          }
        }
        result = andResult;
        break;
      case Op.Or:
        let orResult;
        for (let i = 1; i < line.length; i++) {
          orResult = parseRepoNode(line[i]);
          if (orResult) {
            result = orResult;
            break;
          }
        }
        result = orResult;
        break;
      case Op.Switch:
        // TODO: maybe try to generate a proper switch statement
        // but this requires "eval" or the Function constructor
        const [, switcherNode, ...casePairs] = line;
        const { length } = casePairs;
        const switcher = parseRepoNode(switcherNode);
        let i = 0;
        for (i; i < length; i += 2) {
          if (switcher === parseRepoNode(casePairs[i])) {
            result = parseRepoNode(casePairs[i + 1]);
            break;
          }
        }
        if (i > length && length % 2) {
          result = parseRepoNode(casePairs[length - 1]);
        }
        break;
      default:
        noSpecialCasesFound = true;
    }
  } else {
    noSpecialCasesFound = true;
  }
  if (noSpecialCasesFound) {
    const lineOuts = line.map(parseRepoNode);
    const error = lineOuts.find(out => isError(out));
    if (error) {
      result = error;
    } else {
      const head = lineOuts[0];
      if (isObject(head) || isString(head)) {
        // if two nodes, do property access; if one, just return that node value
        result = 1 in lineOuts ? lineOuts[0][lineOuts[1]] : lineOuts[0];
      } else if (isNumber(head)) {
        const second = lineOuts[1];
        if (isFunc(second)) {
          const rest = lineOuts.slice(2);
          result = second(head, ...rest);
        } else {
          result = head;
        }
      } else if (isBoolean(head)) {
        // TODO: support boolean as first node
      } else if (isFunc(head)) {
        const tail = lineOuts.slice(1);
        result = head(...tail);
      } else {
        result = lineOuts[0];
      }
    }
    outs = lineOuts;
  }
  if (trace) {
    const newTraceLine: TraceLine = {
      path,
      args: scopes[path.slice(0, -1).join() ],
      outs,
      result
    };
    trace.push(newTraceLine);
    if (trace.length > 222) {
      throw new TraceError(trace);
    }
  }
  return result;
}

export function getLastLine(decList: Dec[], currentPath: string[]): Node[] {
  let foundDec;
  let i = decList.length;
  while (!foundDec && --i >= 0) {
    const dec = decList[i];
    if (!dec.id.startsWith("_")) {
      foundDec = dec;
    }
  }
  if (!foundDec) {
    throw new Error("How the hell did this happen?");
  }
  const { id, line } = foundDec;
  if (isDecList(line)) {
    return getLastLine(line, currentPath.concat(id));
  } else {
    return line;
  }
}

export function fillDecReturn(dec: Dec): Dec {
  let { line } = dec;
  if (!isDecList(line)) {
    return dec;
  }
  dec = assoc(dec, "line", line.map(fillDecReturn));
  line = dec.line as Dec[];
  const hasExplicitReturn = line.some(dec => dec.id === "R");
  if (!hasExplicitReturn) {
    let foundDec;
    let i = line.length;
    while (!foundDec && --i >= 0) {
      const dec = line[i];
      if (!dec.id.startsWith("_")) {
        foundDec = dec;
      }
    }
    if (foundDec) {
      dec = assoc(dec, "line", line.concat({ id: "R", line: foundDec.line }));
    }
  }
  return dec;
}

export function makeDict(decList: Dec[], currentPath: string[] = [], dict: DecDict = {}) {
  decList = decList.map(fillDecReturn); // TODO: no need to redo this everytime
  for (const dec of decList) {
    const { id, line } = dec;
    const decPath = currentPath.concat(id);
    if (isDecList(line)) {
      makeDict(line, decPath, dict);
    } else {
      dict[decPath.join() ] = line;
    }
  }
  // use last non-inlined line if 'R' id line not found
  // TODO: logic to use last line could be cleaner
  return dict;
}

type BaseType = {
  nullable?: boolean | string[];
  validatorRef: string[];
  defaultRef: string[];
};

type NumType = BaseType & {
  kind: "N";
  integer?: boolean | string[];
  start?: number | string[];
  end?: number | string[];
  default?: number;
};

type BoolType = BaseType & {
  kind: "B";
  default?: boolean;
};

type StringType = BaseType & {
  kind: "S";
  default?: string;
};

type ObjectType = BaseType & {
  kind: "O";
};

type ValType = NumType | BoolType | StringType | ObjectType;

export type ParamList = {
  type?: ValType;
}[];

export type ParamListSet = HashMap<ParamList>;
export type ArgList = (Val | OpRef | Ref)[];
export type SampleListSet = HashMap<string[]>;

export enum Source {
  Unpkg
}

export type BasePackage = {
  source: Source;
  id: string;
};

export type InitPackage = Partial<BasePackage>;

export type Package = InitPackage & {
  package: any;
};

export type BaseRepo = {
  commits: Commit[];
  sampleLists: SampleListSet;
  paramLists: ParamListSet;
  packages: InitPackage[]; // TODO: this typing seems inconsistent, fix it
};

export function Package(initial: InitPackage): Package {
  const self = observable(safeAssign({ package: null }, initial));
  (async () => {
    // TODO: change import method based on source property
    const pkg = await (window as any).SystemJS.import(self.id);
    self.package = pkg;
  })();
  return self;
}

export type InitRepo = Partial<BaseRepo>;

export type RepoComputed = Readonly<{
  packedMain: PackedSet;
  baseMain: Dec[];
  packed: PackedSet;
  main: Dec[];
  baseDict: DecDict;
  dict: DecDict;
  full: FullDec;
  topMap: HashMap<FullDec>;
  gen: { (path: DecId, trace?: Trace, scopes?: object): any };
  parseNode: { (node: Node, trace?: Trace, scopes?: object): any };
  place: { (placement: Commit): void };
}>;

export type RepoStore = BaseRepo & RepoComputed;

const defaults: BaseRepo = {
  commits: [],
  paramLists: {},
  sampleLists: {},
  packages: []
};

const init = (initial: InitRepo): BaseRepo =>
  safeAssign(defaults, initial, {
    packages: (initial.packages || []).map(Package)
  });

const derive = (
  self: RepoStore // TODO: figure out return type
) =>
  <RepoComputed>{
    get packed() {
      return processCommits(self.commits);
    },
    get main() {
      return unpackSet(self.packed);
    },
    get full() {
      const full: FullDec = {
        id: "",
        path: [],
        line: fillLine(self.main)
      };
      return full;
    },
    get topMap() {
      const { full } = self;
      const topMap = {};
      for (const subLine of full.line) {
        topMap[(subLine as FullDec).id] = subLine;
      }
      return topMap;
    },
    get dict() {
      return makeDict(self.main);
    },
    gen(path, trace, scopes = {}) {
      try {
        return gen(self.dict, path, trace, scopes);
      } catch (ex) {
        return ex;
      }
    },
    parseNode(node, trace, scopes = {}) {
      return parseNode(self.dict, node, trace, scopes);
    },
    place(placement) {
      // TODO: is this method still needed? Just push commits directly now?
      self.commits.push(placement);
    }
  };

export const repoDef = { init, derive };

export function fillLine(line: Line = [], currentPath: string[] = []): FullLine {
  if (!line.length) {
    return [];
  }
  if (isDecList(line)) {
    return line.map((subDec: Dec, index: number): FullDec => {
      const path = [...currentPath, subDec.id];
      return {
        id: subDec.id,
        path,
        line: fillLine(subDec.line, path)
      };
    });
  }
  return line;
}

/********** Packing Functions **********/

export type PackedRef = { r: RefPath };
export type PackedArg = { a: RefPath };
export type PackedNode = any[] | Op | PackedRef | PackedArg | { p: string } | null;

export function packNode(node: Node): PackedNode {
  if (isVal(node)) {
    return [node.val];
  }
  if (isOp(node)) {
    return node.op;
  }
  if (isRef(node)) {
    return { r: node.ref };
  }
  if (isArg(node)) {
    return { a: argPath(node) };
  }
  return null;
}

export type PackedSet =
  | PackedNode[]
  | {
      [decId: string]: PackedSet;
    };

export function packLine(line: Line): PackedSet {
  if (isDecList(line)) {
    const packedSet = {};
    for (const dec of line) {
      packedSet[dec.id] = packLine(dec.line);
    }
    return packedSet;
  }
  return (line as Node[]).map(packNode);
}

export function unpackNode(node: PackedNode, atPath: DecPath = []): Node {
  if (!node) {
    return { val: "ERROR" };
  }
  if (isArray(node)) {
    return { val: node[0] };
  }
  if (isString(node)) {
    return { op: node };
  }
  if ((node as PackedRef).r) {
    let { r } = node as PackedRef;
    if (r.length === 1) {
      // TODO: potential optimization: just create ref instead of wrapping in array
      r = [0, ...r];
    }
    const [first, possibleId] = r;
    const scopePath = atPath.slice(0, -first - 1);
    const ref = scopePath.concat(String(possibleId));
    return { ref };
  }
  if ((node as PackedArg).a) {
    let { a } = node as PackedArg;
    if (a.length === 1) {
      // TODO: potential optimization: just create ref instead of wrapping in array
      a = [0, ...a];
    }
    const [first, arg] = a;
    const scope = atPath.slice(0, -first - 1);
    return { scope, arg } as Arg;
  }
  return { val: "NOT_FOUND" };
}

// TODO: check if memoizing is actually faster
const numerify = memoize((id: string): number => parseInt(id, 36));

function sorter(a: string, b: string) {
  if (b === "R") {
    return -1;
  }
  if (a === "R") {
    return 1;
  }
  const aVal = numerify(a);
  const bVal = numerify(b);
  if (aVal < bVal) {
    return -1;
  }
  if (aVal === bVal) {
    return 0;
  }
  return 1;
}

export function unpackSet(set: PackedSet, atPath: DecPath = []): Line {
  if (isArray(set)) {
    return set.map(node => unpackNode(node, atPath));
  }
  const full = [] as Dec[];
  const setIds = Object.keys(set).sort(sorter);
  for (const id of setIds) {
    full.push({
      id,
      line: unpackSet(set[id], atPath.concat(id))
    });
  }
  return full;
}

/********** Utility Types and Functions **********/

export function asArray(path: DecId): string[] {
  if (isArray(path)) {
    return path;
  }
  return [path];
}
export function asKey(path: DecId): string {
  if (isArray(path)) {
    return path.join() ;
  }
  return path;
}

export function relativize(base: RefPath, goal: RefPath) {
  const goalScopePath = goal.slice(0, -1);
  const goalLength = goal.length;
  const goalId = goal[goalLength - 1];
  const goalScopePathKey = goalScopePath.join() ;
  let levelsUp = 0;
  let currentScopePath = base.slice(0, -1 - levelsUp);
  let currentScopePathKey = currentScopePath.join() ;
  while (currentScopePath.length && currentScopePathKey !== goalScopePathKey) {
    levelsUp++;
    currentScopePath = base.slice(0, -1 - levelsUp);
    currentScopePathKey = currentScopePath.join() ;
  }
  if (levelsUp !== base.length - goalLength) {
    throw new Error("Goal appears to be invalid.");
  }
  const isArg = isNumber(goalId);
  if (!levelsUp) {
    return [isArg ? CommitNode.Arg : CommitNode.Ref, goalId];
  }
  return [isArg ? CommitNode.Arg : CommitNode.Ref, levelsUp, goalId];
}



