import * as React from "react";
import { CSSProperties, HTMLProps, SFC } from "react";
import { observer } from "mobx-react";

import { colors } from "./ui";

export const hue = 199;
export const border = `1px solid hsl(${hue},49%,49%)`;
export const lightBorder = `1px solid hsl(${hue},49%,63%)`;
export const keyXUnit = 80;
export const keyYUnit = 33;
export const maxWidth = keyXUnit * 10;

const containerStyle: CSSProperties = {
  position: "fixed",
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  width: "100vw",
  bottom: 0
};

const keyboardStyle: CSSProperties = {
  display: "flex",
  flexWrap: "wrap",
  width: "100vw",
  maxWidth
};

const baseStyle: CSSProperties = {
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  textAlign: "center",
  borderRight: border,
  borderBottom: border,
  width: "10%",
  height: keyYUnit,
  color: colors.label,
  background: `hsl(${hue},77%,72%)`
};

const fullStyle: CSSProperties = {
  ...containerStyle,
  background: `hsl(${hue},77%,72%)`,
  height: keyYUnit * 3
};

const FullDisplay: SFC<{}> = ({ children = "Input Mode" }) => (
  <div style={fullStyle}>{children}</div>
);

const defaultInputModeEl = <FullDisplay />;

const fullDisplayStyle: CSSProperties = {
  display: "flex",
  flexDirection: "column",
  alignItems: "center"
};

export const ReactKeyboard: any = observer(({ store }) => {
  const { keyMap, heldKeyCoords } = store;
  if (typeof keyMap === "function") {
    return defaultInputModeEl;
  }
  const { title, enter } = keyMap;
  if (title) {
    const children = [<div key="T">{title}</div>];
    if (enter) {
      children.push(<div key="E">Press Enter to {enter}</div>);
    }
    // TODO: display actions for esc, tab if given
    return (
      <FullDisplay>
        <div style={fullDisplayStyle}>{children}</div>
      </FullDisplay>
    );
  }
  const els = [];
  for (let y = 1; y <= 3; y++) {
    const keySetAtY = keyMap[y];
    for (let x = 0; x <= 9; x++) {
      const style = { ...baseStyle };
      if (x === 4) {
        style.background = `hsl(${hue},77%,82%)`;
        style.borderBottom = lightBorder;
        style.borderRight = lightBorder;
      } else if (x === 5) {
        style.background = `hsl(${hue},77%,82%)`;
        style.borderBottom = lightBorder;
      }
      if (heldKeyCoords && heldKeyCoords.x === x && heldKeyCoords.y === y) {
        style.background = `hsl(${hue},77%,66%)`;
      }
      const attrs: HTMLProps<HTMLDivElement> = {
        key: `${x}.${y}`,
        style,
        onClick() {
          store.handleKeyCellFire(x, y);
        }
      };
      if (keySetAtY) {
        const thisKey = keySetAtY[x];
        if (thisKey && thisKey.label) {
          attrs.children = thisKey.label;
          style.cursor = "pointer";
          style.userSelect = "none";
        }
      }
      els.push(<div {...attrs} />);
    }
  }
  return (
    <div style={containerStyle}>
      <div style={keyboardStyle}>{els}</div>
    </div>
  );
});



